button{
    width: 250px;
    height: 80px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: bold;
    color: aliceblue;
    border: 2px solid white;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
    font-size: 35px;
}

button:hover{
    background-color: #32b438;
}

.boton-click{
    background-color: blue;
}

.boton-reinicio{   
    background-color: tomato;
}